
.farma {
  overflow-x: hidden;
}
.farma .headWrap {
  position: absolute;
  top: 0px;
  display: block;
  background-color: #040404;
}
.farma .head {
  background-color: #040404;
}
.farma .title {
  color: #474747;
  font-size: 35px;
  font-weight: 400;
  line-height: 50px;
  text-transform: uppercase;
  letter-spacing: 1.4px;
  text-align: center;
  margin-top: 93px;
}
.farma .textBlWrap {
  width: 100%;
  margin: 62px auto 101px;
  max-width: 1069px;
  overflow: auto;
  position: relative;
  padding: 0px 15px;
}
.farma .textBl {}
.farma .avaWrap {
  float: right;
  width: 160px;
  margin-left: 15px;
}
.farma .ava {
  width: 149px;
  height: 149px;
  border-radius: 50%;
}
.farma .ava_1 {background-image: url(../img/farma_ava_1.png);}
.farma .ava_2 {background-image: url(../img/farma_ava_2.png);}
.farma .ava_3 {background-image: url(../img/farma_ava_3.png);}
.farma .ava_4 {background-image: url(../img/farma_ava_4.png);}
.farma .ava_5 {background-image: url(../img/farma_ava_5.png);}
.farma .ava_6 {background-image: url(../img/farma_ava_6.png);}
.farma .ava_7 {background-image: url(../img/farma_ava_7.png);}
.farma .ava_8 {background-image: url(../img/farma_ava_8.png);}
.farma .ava_9 {background-image: url(../img/farma_ava_9.png);}
.farma .ava_10 {background-image: url(../img/farma_ava_10.png);}
.farma .textBlWrap:nth-child(20) {
  margin-bottom: 200px;
}
.farma .text {
  color: #474747;
  font-size: 16px;
  font-weight: 400;
  line-height: 22px;
  text-align: right;
  float: left;
  width: calc(100% - 200px);
  padding-right: 20px;
}
.farma .textBlWrap:nth-child(2n) .text {
  text-align: left;
  float: right;
  padding-left: 20px;
  padding-right: unset;
}
.farma .text b {
  font-size: 18px;
  font-weight: 700;
  letter-spacing: 0.72px;
}
.farma .text p {
  font-weight: 400;
  letter-spacing: 0.64px;
}
.farma ol {
  padding-left: 25px;
}
.farma li {}
.farma .buyKnowBl {
  position: static;
}
.farma .back {
  position: absolute;
}
.farma .back_1 {
  background-image: url(../img/farma_back_1.png);
  width: 811px;
  height: 364px;
  margin: -157px 0px 0px -405px;
  left: calc(50% + -790px);
  top: 9%;
  background-repeat: no-repeat;
}
.farma .back_2 {
  background-image: url(../img/farma_back_2.png);
  width: 203px;
  height: 208px;
  margin: -104px 0px 0px -101px;
  left: calc(50% + 668px);
  top: 29%;
}
.farma .back_3 {
  background-image: url(../img/farma_back_3.png);
  width: 329px;
  height: 291px;
  margin: -145px 0px 0px -164px;
  left: calc(50% + -717px);
  top: 62%;
}
.farma .back_4 {
  background-image: url(../img/farma_back_4.png);
  width: 404px;
  height: 291px;
  margin: -145px 0px 0px -202px;
  left: calc(50% + 668px);
  top: 88%;
}
@media (max-width: 900px) {
  .farma .buyKnowBl .wrap {
    padding-top: 60px;
  }
  .farma .text {
    width: calc(100% - 160px);
  }
  .farma .textBlWrap .avaWrap {
    margin-left: 0px;
  }
  .farma .textBlWrap:nth-child(2n) .avaWrap {
    margin-right: 0px;
  }
}
@media (max-width: 700px) {
  .farma .text {
    width: 100%
  }
  .farma .textBlWrap .avaWrap {
    display: none;
  }
}
  