
.sec_1 {
  background-color: #0b0b0b;
}
.head {
  margin: 0px auto;
  width: 100%;
  max-width: 1160px;
  padding: 4px 15px 0px;
  display: flex;
  justify-content: space-between;
  position: absolute;
  z-index: 40;
  margin-left: -580px;
  left: 50%;
  background-color: #5a5858;
}
.fp-viewing-0-0 .head{
  background-color: transparent
}
p {
  margin-bottom: 0;
}
.head .leftBL {
  padding-top: 4px;
  width: 367px;
}
.head .leftBL .logo {
  background-image: url(../img/head_logo.png);
  background-repeat: no-repeat;
  background-position: 0px center;
  width: 88px;
  height: 49px;
  float: left;
  border-right: 2px solid #fff;
  margin-right: 17px;
  margin-top: -5px;
  margin-left: 3px;
}
.head .leftBL p {
  color: #f6f6f6;
  font-family: "Proxima Nova";
  font-size: 15px;
  font-weight: 100;
  line-height: 18px;
  letter-spacing: 0.6px;
}
.head .centrBL {
  width: 350px;
  padding-top: 6px;
}
.head .centrBL .icon {
  width: 37px;
  height: 38px;
  border: 1px solid #ff0000;
  float: left;
  border-radius: 50%;
  margin-right: 12px;
}
.head .centrBL svg {
  fill: #fff;
  width: 17px;
  height: 35px;
  margin-left: 10px;
}
.head .centrBL .tel {
  color: #f6f6f6;
  font-family: "Proxima Nova";
  font-size: 18px;
  font-weight: 400;
  line-height: 15.98px;
  text-transform: uppercase;
  /* Text style for "+7 964 232" */
  letter-spacing: 0.72px;
  margin-top: 15px;
  float: left;
  width: 170px;
}
.head .centrBL .call {
  color: #f44c4e;
  font-family: "Proxima Nova";
  font-size: 15px;
  font-weight: 300;
  line-height: 18px;
  text-decoration: underline;
  /* Text style for " " */
  letter-spacing: 0.6px;
  display: block;
  margin-top: 11px;
}
.rightBL {
  color: #f6f6f6;
  font-family: "Proxima Nova";
  font-size: 18px;
  font-weight: 400;
  line-height: 23.98px;
  text-transform: uppercase;
  /* Text style for "" */
  letter-spacing: 0.72px;
  padding-top: 16px;
  cursor: pointer;
}
.slide_01,
.slide_02,
.slide_03 {
  width: 100%;
  height: 100vh;
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
}
.slide_01,
.slide_02,
.slide_03 {
  display: flex;
  align-items: center;
  justify-content: center;
}
.slide_01 {
  background-image: url(../img/slide_01.jpg);
}
.slide_02 {
  background-image: url(../img/slide_02.png);
}
.slide_03 {
  background-image: url(../img/slide_03.png);
}
.slide_01:after,
.slide_02:after,
.slide_03:after {
  content: "";
  position: absolute;
  background-color: #0b0b0b;
  opacity: 0.55;
  width: 100%;
  height: 100%;
  top: 0px;
  left: 0px;
  pointer-events: none;
  z-index: 1;
}
.slide_01 {}
.slide_01 .h2 {
  color: #f8f8f8;
  font-size: 37px;
  font-weight: bold;
  text-transform: uppercase;
  font-weight: 700;
  letter-spacing: 3.4px;
  line-height: 45px;
  margin-bottom: 6px;
}
.slide_01Wrap,
.slide_02Wrap,
.slide_03Wrap {
  display: block;
  min-height: 20px;
  padding: 6px 15px 16px;
  max-width: 980px;
  z-index: 50;
  position: relative;
  width: 100%;
}
.slide_01 .h2.red {
  font-weight: 700;
  letter-spacing: 4.8px;
  line-height: 45px;
  color: #ea3437;
}
.slide_01 .h3 {
  font-family: "Proxima Nova";
  font-size: 28px;
  font-weight: 300;
  letter-spacing: 1.68px;
  line-height: 49px;
  color: #f8f8f8;
  text-transform: uppercase;
  margin-bottom: 38px;
}
.slide_02Wrap .h2 {
  color: #f8f8f8;
  font-family: "Proxima Nova";
  font-size: 46px;
  font-weight: bold;
  line-height: 58px;
  letter-spacing: 4px;
  text-transform: uppercase;
  text-align: right;
  margin-bottom: 0px;
  padding-right: 60px;
}
.slide_03Wrap .h2 {
  color: #f8f8f8;
  font-family: "Proxima Nova";
  font-size: 46px;
  font-weight: bold;
  line-height: 61px;
  letter-spacing: 7px;
  text-transform: uppercase;
  text-align: right;
  margin-bottom: 0px;
  padding-right: 60px;
}
.slide_02Wrap .h2.red {
  color: #ea3437;
  font-weight: bold;
  line-height: 58px;
  font-size: 50px;
  text-transform: uppercase;
  letter-spacing: 4.2px;
  margin-bottom: 120px;
  padding-right: 60px;
}
.slide_03Wrap .h2.red {
  color: #ea3437;
