  font-size: 43px;
  text-transform: uppercase;
  letter-spacing: 2px;
  margin-bottom: 114px;
  padding-right: 60px;
}
.slide_02Wrap,
.slide_03Wrap {
  max-width: 1050px;
}
.slide_Wrap .form {
  max-width: 759px;
