
.headWrap {
    background-color: #040404;
    width: 100%;
    height: 52px;
  }
  .otzivy .head {
    background-color: #040404;
  }
  .otzivy .topBlock {
    padding-top: 26px;
    position: relative;
  }
  .otzivy .topBlock .back {
    color: #afafaf;
    font-size: 15px;
    font-weight: 400;
    line-height: 23px;
    text-decoration: underline;
    letter-spacing: 0.6px;
    position: absolute;
    top: 67px;
    left: 40px;
    margin-left: -15px;
  }
  .otzivy .topBlock .title {
    color: #474747;
    font-size: 35px;
    font-weight: 400;
    line-height: 50px;
    text-transform: uppercase;
    letter-spacing: 1.4px;
    text-align: center;
    margin-bottom: 19px;
  }
  .otzivy .topBlock .btnFiltr {
    position: absolute;
    right: 0px;
    top: 132px;
  }
  .btnFiltr .btn {
    color: #5a5a5a;
    background-image: url(../img/btnFiltr_btn.png);
    background-repeat: no-repeat;
    background-position: 94% center;
    font-size: 15px;
    font-weight: 700;
    line-height: 23px;
    letter-spacing: 0.6px;
    padding: 2px 23px;
    padding-left: 14px;
  }
  .btnFiltr .btn.active {
    background-image: url(../img/btnFiltr_btnA.png);
    background-color: #f6f6f6;
    color: #f13438;
  }
  .otzivy .topBlock .btn {
    color: #5a5a5a;
    font-size: 15px;
    font-weight: 700;
    line-height: 23px;
    letter-spacing: 0.6px;
  }
  .otzivy .userBl {
    margin: 0px auto 0px;
    width: 100%;
    max-width: 1163px;
    overflow: auto;
    padding-bottom: 51px;
    padding-top: 32px;
  }
  .otzivy .userBlWrap:nth-child(2n) {
    background-color: #f5f5f5;
  }
  .otzivy .userBlWrap {
    padding: 0px 15px;
  }
  .otzivy .buyKnowBl {
    position: static;
  }
  .otzivy .userBl .avaWrap {
    width: 20%;
    float: left;
    height: auto;
  }
  .otzivy .userBl .ava {
    width: 129px;
    -webkit-background-size: cover;
    background-size: cover;
    background-position: center;
    height: 128px;
    margin-top: 0px;
  }
  .otzivy .userBl .name {
    color: #f13438;
    font-size: 20px;
    font-weight: 500;
    line-height: 22px;
    letter-spacing: 0.8px;
    margin-top: 13px;
  }
  .otzivy .userBl .text {
    width: 80%;
    float: right;
    margin-bottom: 25px;
    margin-top: 9px;
    letter-spacing: 1.5px;
    line-height: 21px;
  }
  .otzivy .userBl .meta {
    border-top: 2px solid #f13438;
    border-bottom: 2px solid #f13438;
    width: 80%;
    float: right;
    line-height: 32px;
    color: #818181;
    font-size: 16px;
    font-weight: 400;
    line-height: 14px;
    padding: 9px 3px;
    letter-spacing: 0.64px;
  }
  .otzivy .otzivyBlock {
    position: static;
    margin: 0px auto 0px;
  }
  .otzivy .userBl .date {
    float: left;
    margin-right: 18px;
  }
  .otzivy .userBl .city {
    float: left;
    margin-right: 43px;
  }
  .otzivy .userBl .like {
    float: left;
    background-image: url(../img/otzivy_like.png);
    -webkit-background-size: contain;
    background-position: center;
    background-size: contain;
    background-position: 0px center;
    line-height: 14px;
    padding-left: 21px;
    background-repeat: no-repeat;
    margin-right: 20px;
    cursor: pointer;
  }
  .otzivy .userBl .dislike {
    float: left;
    background-image: url(../img/otzivy_dislike.png);
    -webkit-background-size: contain;
    background-position: center;
    background-size: contain;
    background-position: center;
    background-repeat: no-repeat;
    background-position: 0px center;
    line-height: 14px;
    padding-left: 21px;
    margin-right: 20px;
    cursor: pointer;
  }
  @media(max-width: 750px) {
    .otzivy .userBl .avaWrap {
      width: 140px;
    }
    .otzivy .userBl .text,
    .otzivy .userBl .meta {
      width: calc(100% - 160px);
    }
  }
  @media(max-width: 560px) {
    .otzivy .userBl .city {
      margin-right: 13px;
    }
    .otzivy .userBl .name {
      padding-top: 40px;
      width: 100%;
    }
    .otzivy .userBl .avaWrap {
      width: 100%;
      float: none;
    }
    .otzivy .userBl .ava {
      height: 60px;
      width: 60px;
      float: left;
      margin-right: 15px;
    }
    .otzivy .userBl .text,
    .otzivy .userBl .meta {
      width: 100%;
    }
  }
  @media(max-width: 850px) {
    .head .leftBL p,
    .head .centrBL .icon,
    .head .centrBL .tel {
      display: none;
    }
  }
  @media(max-width: 600px) {
    .otzivyBlock {
      width: 100%;
    }
    .otzivyBlock .bt {
      background-position: 10px center;
      padding-left: 36px;
    }
  }
  .bottomBlock {
    position: relative;
    overflow: auto;
    padding-bottom: 177px;
  }
  .bottomBlock .btn {
    width: 115px;
    height: 12px;
    color: #fefefe;
    font-size: 15px;
    font-weight: 400;
    line-height: 26px;
    /* Text style for " " */
    letter-spacing: 0.3px;
    width: 155px;
    height: 42px;
    border-radius: 7px;
    background-color: #454444;
    margin: 24px auto 0px;
    display: block;
  }
  .bottomBlock .pagin {
    position: absolute;
    right: 50px;
    top: 73px;
  }
  .bottomBlock .pagin p {
    color: #5a5a5a;
    font-size: 15px;
    font-weight: 400;
    width: 29px;
    height: 29px;
    line-height: 29px;
    float: left;
    text-align: center;
  }
  .bottomBlock .pagin p.active {
    background-color: #f6f6f6;
  }
  .bottomBlock .pagin p.active.hover {
    font-weight: 700;
    letter-spacing: 0.3px;
  }
  