
.sec_6 {
  background-image: url(../img/sec6_back.png);
  background-repeat: no-repeat;
  background-position: -0% center;
  background-color: #1d1d1d;
  -webkit-background-size: contain;
  background-size: contain;
  color: #fff;
}
.sec_6 .wrapBL {}
.sec_6 .wrapCont {}
.sec_6 .h3 {
  font-size: 30px;
  font-weight: 400;
  line-height: 50px;
  text-transform: uppercase;
  letter-spacing: 1.2px;
  text-align: center;
  position: absolute;
  top: 9%;
  width: 100%;
}
.sec_6 .blWrap {
  margin: 0px auto;
  width: 100%;
  max-width: 1290px;
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
  position: absolute;
  top: 57%;
  margin-top: -242px;
  margin-left: -645px;
  left: 50%;
}
.sec_6 .bl {
  text-align: center;
  float: left;
  width: 25%;
  padding: 0px 10px;
}
.sec_6 .bl_1 {
  width: 24%;
}
.sec_6 .bl_2 {
  width: 26%;
}
.sec_6 .bl_3 {
  width: 26%;
}
.sec_6 .bl_4 {
  width: 24%;
}
.sec_6 .bl .title {
  line-height: 21.3px;
  letter-spacing: 0.97px;
  text-align: center;
  font-weight: 400;
  font-size: 21px;
  margin-bottom: 41px;
  padding: 0px 15px;
}
.sec_6 .bl .img {
  height: 211px;
  background-repeat: no-repeat;
  background-position: center;
  margin-bottom: 37px;
  -moz-transition: all 0.3s ease-out;
  -o-transition: all 0.3s ease-out;
  -webkit-transition: all 0.3s ease-out;
}
.sec_6 .bl .img1 {
  background-image: url(../img/sec6_img1.png);
  background-position: 64% 39%;
}
.sec_6 .bl .img2 {
  background-image: url(../img/sec6_img2.png);
  background-position: 78% 69%;
}
.sec_6 .bl .img3 {
  background-image: url(../img/sec6_img3.png);
}
.sec_6 .bl .img4 {
  background-image: url(../img/sec6_img4.png);
}
.sec_6 .bl:hover .img {
  -webkit-transform: scale(1.3);
  -moz-transform: scale(1.3);
  -o-transform: scale(1.3);
}
.sec_6 .bl p {
  font-size: 16px;
  font-weight: 400;
  line-height: 22px;
  letter-spacing: 0.64px;
  padding: 0px 15px;
}
