  .sec_7 .infoBl .name {
    color: #474747;
    font-size: 22px;
    font-weight: 400;
    line-height: 22px;
    letter-spacing: 0.88px;
    display: block;
    margin-bottom: 20px;
    width: calc(100% + 30px);
  }
