  .sec_7 .infoBl .line {
    height: 3px;
    background-color: #f13438;
    position: relative;
  }
  .sec_7 .infoBl .line:after{
    content:'';
    background-image: url(../img/sec_7Line.png);
    width: 23px;
    height: 15px;
    position: absolute;
    left: 70px;
    top: -11px;
    background-color: whitesmoke;}
  .sec_7 .infoBl .text {
    color: #474747;
    font-size: 16px;
    font-weight: 400;
    line-height: 20px;
    letter-spacing: 0.64px;
